$(()=> {
    const $jsFancy = $('.js-fancy');

    $jsFancy.on('click', e=> {
        console.log(e)
        e.preventDefault();
        Fancybox.show([
            {
                src: e.currentTarget.dataset.src || e.currentTarget.getAttribute('data-src'),
                type: "inline",
            }
        ])
    })
})